a {
  color: #000;
  letter-spacing: 2px;
  font-size: 12px;
  font-weight: bold;
}

a:hover {
  opacity: .7;
  text-decoration: underline;
}

.main__pricing {
  width: 100%;
  background-color: #f0f0f0;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  padding: 80px 90px;
  display: grid;
}

.main__pricing img {
  max-width: 100%;
}

.main__pricing .main__pricing-card__text {
  height: 230px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.main__pricing .main__pricing-card__text h3 {
  text-transform: uppercase;
  color: #000;
  letter-spacing: normal;
  margin-top: 28px;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.main__pricing .main__pricing-card__text h4 {
  color: #000;
  letter-spacing: 1px;
  font-size: 12px;
  font-weight: 300;
  line-height: 19px;
}

.main__pricing .main__pricing-card__text button {
  width: 188px;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 1px;
  background-color: #3c3c3b;
  border: 1px solid #3c3c3b;
  justify-content: space-between;
  align-items: center;
  margin: 30px auto 24px 0;
  padding: 15px 20px 12px;
  font-size: 12px;
  font-weight: bold;
  transition-duration: .5s;
  display: flex;
  position: relative;
}

.main__pricing .main__pricing-card__text button:hover {
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
}

.main__pricing .main__pricing-card__text button .fa-arrow-right-long {
  font-size: 18px;
  display: inline-block;
  position: absolute;
  bottom: 10px;
  left: 20px;
}

.main__pricing .main__pricing-card__text button .fa-car {
  font-size: 16px;
}

.main__pricing .priceEnd {
  height: 150px;
}

@media screen and (max-width: 1000px) {
  .main__pricing {
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(2, 1fr);
    padding: 50px 40px 0;
  }
}

@media screen and (max-width: 700px) {
  .main__pricing {
    grid-template-rows: repeat(7, 1fr);
    grid-template-columns: 1fr;
    padding: 50px 20px 0;
  }
}

